.switch {
  display: inline-block;
  position: relative;
  top: 2px;
}

.switch__input {
  -ms-appearance: none;
  appearance: none;
  position: absolute;
}

.switch__input:checked + .switch__slider:after {
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .switch__input:checked + .switch__slider:after {
    background-image: url("sun-check-18.0cebd94f.svg");
    left: 43px;
  }
}

@media screen and (max-width: 767px) {
  .switch__input:checked + .switch__slider:after {
    background-image: url("sun-check-12.7ef0be73.svg");
    left: 28px;
  }
}

.switch__slider {
  display: inline-block;
  position: relative;
}

