  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .switch__slider:after {
    content: "";
    width: 18px;
    height: 18px;
    background-image: url("moon-check-18.95e9bfc2.svg");
    position: absolute;
    top: 6px;
    left: 5px;
  }
}

